@charset "UTF-8";
:root {
  --orange: #D45B00;
  --red: #C80A00;
}

/* ==============================
  || breakpoints
============================== */
/* ==============================
  中央寄せ
============================== */
body {
  font-family: "Noto Serif JP", sans-serif;
  font-size: 16px;
  letter-spacing: 0.9px;
}

.sans {
  font-family: "Noto Sans JP", sans-serif;
}

/* =================================
  background
================================== */
.bg-orange {
  background: var(--orange);
}
.bg-orange.pats {
  position: relative;
}
.bg-orange.pats:before {
  position: absolute;
  top: -12px;
  left: 0;
  content: "";
  display: block;
  width: 100%;
  height: 10px;
  background: var(--orange);
  border-bottom: 2px solid #000;
}
.bg-orange.pats:after {
  bottom: -12px;
  left: 0;
  content: "";
  display: block;
  width: 100%;
  height: 10px;
  background: var(--orange);
  border-top: 2px solid #000;
}

/* =================================
  ||title
================================== */
.page-title {
  background-image: url("../images/bg/texture_title_w600.webp");
  background-position: 0 0;
  background-repeat: repeat;
  background-size: contain;
  text-align: center;
  color: #342622;
  line-height: 1.5;
  letter-spacing: 1.8px;
  font-size: 1.5rem;
  padding: 1.75rem 0 3.75rem;
}
.page-title span {
  font-size: 1.125rem;
  display: block;
}

/* =================================
  Headline
================================== */
.hl-red {
  font-size: 1rem;
  line-height: 2;
  letter-spacing: 4px;
  background: var(--red);
  text-align: center;
  font-weight: 600;
  margin-bottom: 2rem;
}

/* =================================
  container
================================== */
.wrap__main {
  position: relative;
}
.wrap__main:before {
  content: "";
  position: absolute;
  display: block;
  background-image: url(../images/parts/circle.svg);
  background-repeat: no-repeat;
  background-size: 120% 6vw;
  background-position: center center;
  width: 100%;
  height: 6vw;
  max-height: 100px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -4vw;
}
.wrap__main::after {
  content: "";
  position: absolute;
  display: block;
  background-image: url(../images/parts/circle.svg);
  background-repeat: no-repeat;
  background-size: 120% 6vw;
  background-position: center center;
  width: 100%;
  height: 6vw;
  max-height: 100px;
  bottom: -4vw;
  left: 50%;
  transform: rotateX(180deg) translateX(-50%);
}

.container {
  width: 86%;
  max-width: 840px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 3.5rem;
  padding-bottom: 6rem;
}
.container.--sm {
  width: 80%;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}
.container.--lg {
  width: 93%;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 2rem;
}

/* =================================
  inner
================================== */
/* =================================
  box
================================== */
/* =========================
  index
========================= */
.logo__top {
  width: 74.78px;
  margin: 2rem auto;
}

.catch {
  font-weight: 600;
  letter-spacing: 6.6px;
}
.catch span {
  display: inline-block;
  position: relative;
}
.catch span:before {
  content: "";
  position: absolute;
  display: block;
  top: -0.5rem;
  left: -1rem;
  width: 10px;
  height: 25px;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
}
.catch span:after {
  content: "";
  position: absolute;
  display: block;
  bottom: -0.5rem;
  right: -1rem;
  width: 10px;
  height: 25px;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
}

@media screen and (min-width: 850px) {
  .introduction {
    display: grid;
    grid-template-columns: calc((100% - 170px) / 2) 105px calc((100% - 170px) / 2);
    align-items: center;
    gap: 2rem;
  }
  .introduction.container.--lg {
    max-width: 1200px;
  }
  .introduction .logo__top {
    width: 106px;
  }
  .introduction .catch {
    font-size: 2rem;
    letter-spacing: 9.6px;
    justify-self: center;
  }
  .introduction .text {
    text-align: left;
    margin-left: 1rem;
  }
}
.slider_index {
  margin-bottom: 6rem;
}
.slider_index .slick-slide {
  margin: 0 20px;
}
@media screen and (min-width: 850px) {
  .slider_index {
    max-width: 840px;
    margin-left: auto;
    margin-right: auto;
  }
  .slider_index .slick-slide {
    margin: 0;
  }
}

.noren {
  position: relative;
  max-width: 1200px;
}
.noren:before {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -35px;
  width: 100%;
  height: 60px;
  font-weight: 600;
  font-size: 0.75rem;
  letter-spacing: 0.2em;
  content: "当店自慢の人気メニュー";
  text-align: center;
  line-height: 60px;
  display: inline-block;
  background-image: url("../images/parts/noren.webp");
  background-size: contain;
  background-position: center center;
}
@media screen and (min-width: 850px) {
  .noren {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(2, auto);
    gap: 10px;
  }
  .noren .food_index--lh-lv2 {
    grid-column: 1/4;
    grid-row: 1/2;
  }
  .noren:before {
    font-size: 1rem;
  }
}

.food_index {
  position: relative;
  z-index: 1;
  margin-top: 85px;
  text-align: center;
}
@media screen and (min-width: 850px) {
  .food_index {
    grid-row: 2/3;
  }
}
.food_index--lh-lv2 {
  font-size: 1.375rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  text-align: center;
}
.food_index:before {
  z-index: -1;
  content: "";
  width: 238px;
  height: 85px;
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -55px;
  background-position: center center;
  background-size: contain;
}
@media screen and (min-width: 850px) {
  .food_index.--001 {
    grid-column: 1/2;
  }
}
.food_index.--001:before {
  background-image: url(../images/index/food_motsu.webp);
}
@media screen and (min-width: 850px) {
  .food_index.--002 {
    grid-column: 2/3;
  }
}
.food_index.--002:before {
  background-image: url(../images/index/food_mizu.webp);
}
@media screen and (min-width: 850px) {
  .food_index.--003 {
    grid-column: 3/4;
  }
}
.food_index.--003:before {
  background-image: url(../images/index/food_ramen.webp);
}

@media screen and (min-width: 850px) {
  .food_index--lh-lv2 {
    letter-spacing: 0.3em;
  }
}
.career__hl-lv2 {
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 0.75rem 0;
  text-align: center;
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1.25rem;
  letter-spacing: 0.05em;
}
.career .name {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 0.5rem;
}
@media screen and (min-width: 850px) {
  .career .name {
    text-align: left;
  }
}
.career .name span {
  font-size: 0.75rem;
  display: inline-block;
  margin-right: 0.25rem;
}
.career__box {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(2, auto);
  gap: 10px;
}
.career__textBox {
  grid-column: 1/3;
  grid-row: 1/2;
}
.career__img--human {
  grid-column: 2/3;
  grid-row: 2/3;
  margin-left: -50px;
}
.career__img--soup {
  grid-column: 1/2;
  grid-row: 2/3;
}
@media screen and (min-width: 850px) {
  .career__hl-lv2 {
    font-size: 1.375rem;
    padding: 1.25rem 0;
    letter-spacing: 0.3em;
  }
  .career__textBox {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .career__img--human {
    grid-column: 2/3;
    grid-row: 1/3;
    margin-left: -30px;
  }
}

/* =========================
  ||FOOD
========================= */
.food-menu * {
  font-size: 0.875rem;
}
.food-menu__pickup figure {
  margin-bottom: 1rem;
}
.food-menu__item {
  margin-bottom: 2rem;
}
.food-menu__item--sub {
  margin-top: 1rem;
  padding: 1rem;
  border: 1px solid #fff;
}
.food-menu__item > dd {
  padding-left: 1rem;
}
.food-menu__title {
  color: var(--orange);
  font-size: 1rem;
}
.food-menu__title--sub {
  text-align: center;
}

.food-foot {
  border: 1px solid #fff;
  padding: 0.5rem 1rem;
  margin-bottom: 2rem;
}

@media screen and (min-width: 850px) {
  .food-menu * {
    font-size: 1rem;
  }
  .food-menu__pickup {
    display: flex;
    justify-content: space-between;
  }
  .food-menu__pickup > * {
    width: 48%;
  }
  .food-menu__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 6rem;
  }
  .food-menu__box {
    width: 48%;
  }
  .food-menu__title {
    font-size: 1.25rem;
  }
  .food-foot {
    margin-top: -4rem;
    margin-bottom: 6rem;
  }
}
/* menu design */
dl.food-menu {
  width: 100%;
  font-size: 1rem;
}

dl.food-menu div {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  padding: 5px 0;
  align-items: center;
}

dl.food-menu div::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  z-index: 1;
  width: 100%;
  border-bottom: dotted 1px #fff;
}

dl.food-menu dt {
  margin: 0;
  padding: 0 5px 0 0;
  background-color: #000;
  z-index: 2;
  text-align: left;
}

dl.food-menu dd {
  margin: 0;
  padding: 0 0 0 5px;
  background-color: #000;
  z-index: 2;
  text-align: right;
}

/* =========================
  ||takeout
========================= */
.takeout__catch {
  line-height: 2;
  letter-spacing: 2.1px;
}
.takeout__logo {
  width: 80%;
  max-width: 352.98px;
  margin: 1.5rem auto;
}
.takeout__image {
  position: relative;
  width: 250px;
  margin-left: auto;
  margin-right: auto;
}
.takeout__image:before {
  content: "";
  position: absolute;
  display: block;
  width: 150px;
  height: 120px;
  background-image: url("../images/takeout/text.webp");
  background-size: contain;
  background-repeat: no-repeat;
  bottom: 120px;
  left: -25px;
}
.takeout__image:after {
  content: "";
  position: absolute;
  display: block;
  width: 100px;
  height: 100px;
  background-image: url("../images/takeout/24h.webp");
  background-size: contain;
  background-repeat: no-repeat;
  bottom: 80px;
  right: -25px;
}

@media screen and (min-width: 850px) {
  .takeout__hero {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: repeat(2, auto);
  }
  .takeout__catch {
    align-self: flex-end;
  }
  .takeout__image {
    grid-column: 1/2;
    grid-row: 1/3;
    width: 348px;
    margin-right: 0;
  }
  .takeout__image:before {
    width: 200px;
    height: 140px;
    top: 120px;
    left: -25px;
  }
  .takeout__image:after {
    width: 136px;
    height: 136px;
    bottom: 80px;
    left: -65px;
  }
}
.takeout-menu__wrap {
  margin-bottom: 2.25rem;
}
.takeout-menu__price {
  font-size: 1.25rem;
}
@media screen and (min-width: 850px) {
  .takeout-menu__price {
    font-size: 1.375rem;
  }
}

@media screen and (min-width: 850px) {
  .machine-menu {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .machine-menu > * {
    width: 48%;
  }
}
/* =========================
  ||shopInfo
========================= */
.page_shopInfo .container {
  padding-bottom: 2rem;
}

.shopInfo__wrap {
  margin-bottom: 4rem;
}
.shopInfo__def {
  border-top: 1px solid #fff;
  padding: 1.5rem 0;
  text-align: center;
  font-size: 0.75rem;
  line-height: 2;
}
@media screen and (min-width: 850px) {
  .shopInfo__def {
    display: grid;
    grid-template-columns: 150px 1fr;
    text-align: left;
    font-size: 1rem;
    padding: 1.5rem 0 1.5rem 1rem;
  }
}

/* =========================
  ||MAP
========================= */
#gmap {
  aspect-ratio: 16/9;
  width: 100%;
  max-width: 800px;
  height: 100vw;
  max-height: 518px;
  margin-bottom: 6rem;
}

@media screen and (min-width: 801px) {
  .map {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 8rem;
  }
}
/* =========================
  ||slider
========================= */
.slick-prev {
  left: 25px;
  z-index: 101;
}

.slick-next {
  right: 25px;
}

@media screen and (min-width: 650px) {
  .slider_index {
    margin-bottom: 6rem;
  }
  .slider_index img {
    width: 75%;
    max-width: 260px;
    margin-left: auto;
    margin-right: auto;
  }
}